import React, { useState } from 'react';
import {
  TextField,
  Button,
  Container,
  Typography,
  Box,
} from '@mui/material';



function Second({ setStage }: { setStage: (stage: number) => void }) {
  const [isLoading, setIsLoading] = useState(false);
  const [actionName, setActionName] = useState('');
  const [contractAddress, setContractAddress] = useState('');
  const [actions, setActions] = useState(['', '', '', '']);
  const [data, setData] = useState(['', '', '', '']);
  const [cpu, setCpu] = useState('');
  const [paymentMethod, setPaymentMethod] = useState('wax');

  const handleActionChange = (index: number, value: string) => {
    const newActions = [...actions];
    newActions[index] = value;
    setActions(newActions);
  };

  const handleDataChange = (index: number, value: string) => {
    const newData = [...data];
    newData[index] = value;
    setData(newData);
  };

  const addActionField = () => {
    setActions([...actions, '']);
    setData([...data, '']);
  };

  const handleSubmit = () => {
    console.log('Submitted:', {
      actionName,
      contractAddress,
      actions,
      data,
      cpu,
      paymentMethod,
    });
    setStage(1);
    // Implement your submit logic here
  };

  const handleReset = () => {
    setActionName('');
    setContractAddress('');
    setActions(['', '', '', '']);
    setData(['', '', '', '']);
    setCpu('');
    setPaymentMethod('wax');
  };

  return (
    <Box sx={{ backgroundColor: '#4A1E2A', padding: '20px', color: 'white' }}>
      <Container maxWidth="md">
        <Box mt={2} display="flex" justifyContent="flex-end">
          <Box sx={{ flex: 1 }}>
            <Typography variant="h4" component="h1" gutterBottom >
              LimitlessWAX
            </Typography>
          </Box>

          <Button variant="contained" sx={{ backgroundColor: '#831F3F', color: 'white' }} onClick={() => setStage(1)}>Save</Button>
          <Button variant="contained" sx={{ backgroundColor: '#831F3F', color: 'white' }} style={{ marginLeft: '10px' }} onClick={() => setStage(1)}>Load</Button>
        </Box>


        <Box sx={{ mt: 2, width: '50%' }}>
          <Typography variant="h4" component="h1" gutterBottom >
            Name this action
          </Typography>
          <TextField
            fullWidth
            margin="normal"
            variant="outlined"
            value={actionName}
            onChange={(e) => setActionName(e.target.value)}
            sx={{ backgroundColor: '#831F3F' }}
          />
        </Box>


        <Box sx={{ mt: 2, width: '50%' }}>
          <Typography variant="h4" component="h1" gutterBottom >
            Smart contract address
          </Typography>
          <TextField
            fullWidth
            margin="normal"
            variant="outlined"
            value={contractAddress}
            onChange={(e) => setContractAddress(e.target.value)}
            sx={{ backgroundColor: '#831F3F' }}
          />
        </Box>

        <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
          {actions.map((action, index) => (
            <Box key={index} sx={{ display: 'flex', gap: 2 }}>
              <Box sx={{ flex: 1 }}>
                <Typography variant="h4" component="h1" gutterBottom >
                  Action #${index + 1}
                </Typography>
                <TextField
                  fullWidth
                  variant="outlined"
                  value={action}
                  onChange={(e) => handleActionChange(index, e.target.value)}
                  sx={{ backgroundColor: '#831F3F' }}
                />
              </Box>
              <Box sx={{ flex: 1 }}>
                <Typography variant="h4" component="h1" gutterBottom >
                  Data #${index + 1}
                </Typography>
                <TextField
                  fullWidth
                  variant="outlined"
                  value={data[index]}
                  onChange={(e) => handleDataChange(index, e.target.value)}
                  sx={{ backgroundColor: '#831F3F' }}
                />
              </Box>
            </Box>
          ))}
        </Box>

        <Button variant="outlined" sx={{ backgroundColor: '#831F3F', color: 'white' }} onClick={addActionField} style={{ marginTop: '10px' }}>
          Add Action +
        </Button>

        <Box sx={{ display: 'flex', gap: 2, mt: 2 }}>
          <Box sx={{ flex: 1 }}>
            <Typography variant="h4" component="h1" gutterBottom >
              How much CPU
            </Typography>
            <Box sx={{ display: 'flex', gap: 2 }}>
              <TextField
                fullWidth
                variant="outlined"
                value={cpu}
                onChange={(e) => setCpu(e.target.value)}
                sx={{ backgroundColor: '#831F3F' }}
              />
              <Typography variant="h4" component="h1" gutterBottom >
                ms
              </Typography>
            </Box>
          </Box>
          <Box sx={{ flex: 1 }}>
            <Typography variant="h4" component="h1" gutterBottom >
              Pay with
            </Typography>
            <Box sx={{ display: 'flex', gap: 2 }}>
              <Button variant="contained" sx={{ backgroundColor: '#831F3F', color: 'white' }} onClick={() => setPaymentMethod('wax')}>WAX</Button>
              <Button variant="contained" sx={{ backgroundColor: '#831F3F', color: 'white' }} onClick={() => setPaymentMethod('custom')}>Custom Token +</Button>
            </Box>
          </Box>
        </Box>

        <Box mt={3}>
          <Button variant="contained" sx={{ backgroundColor: '#831F3F', color: 'white' }} onClick={handleSubmit} style={{ marginRight: '10px' }}>
            Submit
          </Button>
          <Button variant="outlined" sx={{ backgroundColor: '#831F3F', color: 'white' }} onClick={handleReset}>
            Reset
          </Button>
        </Box>


      </Container>
    </Box>
  );
}

export default Second;